//BUTTON
.button-container {
    padding: 0px;
    margin-right: 40px;

    .switch {

        position: relative;
        display: inline-block;
        width: 60px;
        height: 34px;
    }

    /* Hide default HTML checkbox */
    .switch input {
        opacity: 0;
        width: 0;
        height: 0;
    }

    /* The slider */
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
    }

    .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }

    input:checked+.slider {
        background-color: #005496;
    }

    input:focus+.slider {
        box-shadow: 0 0 1px #005496;
    }

    input:checked+.slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }

    /* Rounded sliders */
    .slider.round {
        border-radius: 34px;
    }

    .slider.round:before {
        border-radius: 50%;
    }
}




//PROFILE PAGE

.darkProfilePage {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: #0A244D;

    .darkProfilePageBody {
        width: 100%;
        background: linear-gradient(to bottom, #081d3e, #0A244D 11%);
        flex: 1 1 auto;
    }

    .allPageHeader {
        flex: 0 0 110px;
        background-color: #0A244D;

        .page_header {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .header-image-container {
            background-color: #0A244D;
            width: 100%;
            height: auto;
            margin: 0;

            display: flex;
            justify-content: center;

            @include forTablet {
                justify-content: flex-start;
                padding-left: 40px;
            }
        }
    }
}

@media (min-width: 768px) {
    .darkProfilePage {


        .darkProfilePageBody {
            padding: 20px 40px;
            display: grid;
            grid-template-columns: 329px 1fr;
            grid-template-rows: 1fr;
            grid-gap: 30px;

            .grid__element--1 {
                background: transparent;
                grid-column: 1/2;
                grid-row: 1/2;
            }

            .grid__element--2 {
                grid-column: 2/3;
                grid-row: 1/2;
                border-radius: 5px;
            }
        }
    }
}

@media (min-width: 1200px) {
    .darkProfilePageBody {
        grid-template-columns: 30vw 1fr;
    }
}



.darkProfilePageBody {

    width: 100%;
    background: linear-gradient(to bottom, #081d3e, #0A244D 11%);

    @include forTablet {
        padding: 20px 40px;
        display: grid;
        grid-template-columns: 329px 1fr;
        grid-template-rows: 1fr;
        grid-gap: 30px;

        .grid-1 {
            background: transparent;
            grid-column: 1/2;
            grid-row: 1/2;
        }

        .grid-2 {
            grid-column: 2/3;
            grid-row: 1/2;
            border-radius: 5px;
        }
    }

    @include forPc {
        grid-template-columns: 30vw 1fr;
    }
}

.darkAllPageFooter {
    width: 100%;
    background-color: #0A244D;
    border-top: solid 1px $borderLineColor;
    flex: 0 0 111px;

    @include forTablet {
        margin-top: 0px;
    }
}


//Styles form box
.DarkcollapseTitle {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;

    .form__title {
        font-family: $mainText;
        font-weight: 600;
        text-transform: uppercase;
        font-size: 18px;
        color: white;
        margin: 10px 0;
    }

    .form__arrow--collapse {
        transform: rotate(180deg);
    }

    .form__icon {
        margin-right: 10px;
        color: #b90082;

    }

    .form__arrow--design,
    .form__arrow--fill,
    .form__arrow--share {
        color: white;
        border: none;
        background-color: transparent;
        font-weight: lighter;
        font-size: $mainTitleSize;
    }
}

.rotate {

    transform: rotate(180deg);
    transition: all 0.1s ease 0.1s;

    .Darkicon {
        color: #b90082;
    }
}

//Styles Preview box
.darkCardPreview {
    position: relative;
    min-height: 457px;
    width: 100%;
    background: linear-gradient(#005496, #032c4d);
}

.cardPreview{
    &__centeredBox {
      @include centerAll;
  
      .DarkpreviewResetButton{
        width: 75px;
        height: 30px;
        background-color: transparent;
        border: 1px solid $whiteColor;
        border-radius: 15px;
        font-family: $buttonFont;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 400;
        line-height: 20px;
        color: white;
        margin-bottom: 20px;
      }
  
      .card {
        width: 240px;
        height: 329px;
        background-color: white;
        padding-top: 21px;
        padding-bottom: 12px;
        box-shadow: 0 1px 10px 0 $shadowColor;
  
        .card__profileInfo--box {
          width: 100%;
          height: 42px;
          padding-left: 15px;
          margin-bottom: 10px;
  
          .card__profileInfo--name {
            font-family: $nameFont;
            font-weight: 700;
            font-size: 18px;
            line-height: 20px;
            margin-bottom: 2px;
          }
  
          .card__profileInfo--job {
            font-family: $nameFont;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
          }
        }
  
        .card__avatar--box {
          width: 100%;
          height: 200px;
          background-color: $previewBackground2;
          background-position: center;
          background-size: cover;
          margin-bottom: 10px;
        }
  
        .card__socialbuttons--list {
          padding-left: 20px;
          list-style: none;
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          align-items: center;
  
          .card__socialbuttons--box {
            display: inline-block;
            position: relative;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            margin-right: 10px;
  
            .hidden {
              display: none;
            }
  
            .icon {
              @include centerAll;
              font-size: 18px;
            }
          }
        }
      }
    }
