//General structure

$buttonFont: $mainText;
$nameFont: $previewText;

.profilePage {
    display: flex;
    flex-direction: column;
    height: 100vh;

    .profilePageBody {
        width: 100%;
        background: linear-gradient(to bottom, #f5f5f5, #ffffff 11%);
        flex: 1 1 auto;
    }
}

@media (min-width: 768px) {
    .profilePage {
        

        .profilePageBody {
            padding: 20px 40px;
            display: grid;
            grid-template-columns: 329px 1fr;
            grid-template-rows: 1fr;
            grid-gap: 30px;

            .grid__element--1 {
                background: transparent;
                grid-column: 1/2;
                grid-row: 1/2;
            }

            .grid__element--2 {
                grid-column: 2/3;
                grid-row: 1/2;
                border-radius: 5px;
            }
        }
    }
}

@media (min-width: 1200px) {
    .profilePageBody {
        grid-template-columns: 30vw 1fr;
    }
}












.allPageHeader {
    flex: 0 0 110px;
    background-color: #ffffff;

    .page_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.profilePageBody {

    width: 100%;
    background: linear-gradient(to bottom, #f5f5f5, #ffffff 11%);

    @include forTablet {
        padding: 20px 40px;
        display: grid;
        grid-template-columns: 329px 1fr;
        grid-template-rows: 1fr;
        grid-gap: 30px;

        .grid-1 {
            background: transparent;
            grid-column: 1/2;
            grid-row: 1/2;
        }

        .grid-2 {
            grid-column: 2/3;
            grid-row: 1/2;
            border-radius: 5px;
        }
    }

    @include forPc {
        grid-template-columns: 30vw 1fr;
    }
}

.allPageFooter {
    width: 100%;
    background-color: #ffffff;
    border-top: solid 1px $borderLineColor;
  flex: 0 0 111px;

    @include forTablet {
        margin-top: 0px;
    }
}


//Styles form box
.formList {
    height: 100%;
    width: 100%;
    padding: 30px;

    .form {
        font-family: $mainText;
        background: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
        padding: 30px 27px;

        a {
            font-family: $mainText;
            text-decoration: none;
        }

        .form__icon {
            color: $secondaryColor;
            margin-right: 10px;
        }

        .form__arrow--design,
        .form__arrow--fill,
        .form__arrow--share {
            color: $primaryColor;
            border: none;
            background-color: transparent;
            font-weight: lighter;
            font-size: $mainTitleSize;
        }

        .form__arrow--collapse {
            transform: rotate(180deg);
        }

        .form__title {
            font-family: $mainText;
            font-weight: 600;
            text-transform: uppercase;
            font-size: 18px;
            color: $primaryColor;
            margin: 10px 0;
        }

        .form__open {
            padding-bottom: 20px;
        }

        .form__sections {
            border-bottom: 1px solid $borderLineColor;
        }
    }
}

//Styles Preview box
.cardPreview {
    position: relative;
    min-height: 457px;
    width: 100%;
    background: linear-gradient($previewBackground1, $previewBackground2);

    &__centeredBox {
        @include centerAll;

        .previewResetButton {
            width: 75px;
            height: 30px;
            background-color: transparent;
            border: 1px solid $whiteColor;
            border-radius: 15px;
            font-family: $buttonFont;
            text-transform: uppercase;
            font-size: 12px;
            font-weight: 400;
            line-height: 20px;
            color: $previewTextColor;
            margin-bottom: 20px;
        }

        .card {
            width: 240px;
            height: 329px;
            background-color: white;
            padding-top: 21px;
            padding-bottom: 12px;
            box-shadow: 0 1px 10px 0 $shadowColor;

            .card__profileInfo--box {
                width: 100%;
                height: 42px;
                padding-left: 15px;
                margin-bottom: 10px;

                .card__profileInfo--name {
                    font-family: $nameFont;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 20px;
                    margin-bottom: 2px;
                }

                .card__profileInfo--job {
                    font-family: $nameFont;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 20px;
                }
            }

            .card__avatar--box {
                width: 100%;
                height: 200px;
                background-color: $previewBackground2;
                background-position: center;
                background-size: cover;
                margin-bottom: 10px;
            }

            .card__socialbuttons--list {
                padding-left: 20px;
                list-style: none;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;

                .card__socialbuttons--box {
                    display: inline-block;
                    position: relative;
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    margin-right: 10px;

                    .hidden {
                        display: none;
                    }

                    .icon {
                        @include centerAll;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}